<template>
  <div class="scroll" ref='wrapper'>
    <div class="content" :style="{top: pullDownStyle + 'px', height: contentHeight}" ref="content">
      <slot></slot>
      <div class="top_tip" v-if="pullRefreshObj.pulldown">
        <div class="before_trigger" v-if="beforePullDown">
          <bubble :y="bubbleY"></bubble>
        </div>
        <div class="after_trigger" v-else>
          <div v-if="pullRefreshObj.finishPullDown" class="msg_txt"></div>
          <div class="msg_icon" v-else>
            <loading></loading>
          </div>          
        </div>
      </div>
      <div class="bottom_tip" v-if="pullRefreshObj.pullup">
        <div class="before_trigger" v-if="pullRefreshObj.finishPullUp">
          <span v-if="!pullRefreshObj.noMoreData"></span>
          <span v-if="pullRefreshObj.noMoreData"></span>
        </div>
        <div class="after_trigger" v-else>
          <div class="msg_icon">
            <loading></loading>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import Bubble from './components/bubble'
import Loading from './components/loading'
export default {
  props: {
    /**
     * 1 scroll
     * 2 scroll
     * 3 scrollswipescroll */
    probeType: {
      type: Number,
      default: 2
    },
    /** * click */
    click: {
      type: Boolean,
      default: true
    },
    /** *  */
    scrollX: {
      type: Boolean,
      default: false
    },
    /** *  */
    listenScroll: {
      type: Boolean,
      default: false
    } /** *  */,
    data: {
      type: Array,
      default: null
    },
    /** *  */
    pullRefreshObj: {
      type: Object,
      default: () => {
        return {
          pulldown: false,
          finishPullDown: true,
          pullup: false,
          finishPullUp: true,
          noMoreData: false
        }
      }
    },
    /** *  */
    beforeScroll: {
      type: Boolean,
      default: false
    },
    /** * scroll */
    refreshDelay: {
      type: Number,
      default: 20
    }
  },
  data () {
    return {
      beforePullDown: true, // 
      isPullUpLoad: false, // 
      bubbleY: 0,
      pullDownStyle: 0, // 
      contentHeight: 'auto' // 
    }
  },
  components: {
    Bubble,
    Loading
  },
  mounted () {
    // console.log(this.$refs.wrapper.clientHeight)
    // console.log(this.$refs.content.clientHeight)
    // DOMbetter-scroll
    setTimeout(() => {
      this._initScroll()
    }, 20)
  },
  methods: {
    _initScroll () {
      if (!this.$refs.wrapper) {
        return
      }
      // better-scroll
      this.scroll = new BScroll(this.$refs.wrapper, {
        probeType: this.probeType,
        click: this.click,
        scrollX: this.scrollX
      })
      // 
      if (this.listenScroll) {
        this.scroll.on('scroll', pos => {
          // 
          if (this.pullRefreshObj.pulldown) {
            this.bubbleY = Math.max(0, pos.y - 50)
            // console.log(pos.y)
          }
          this.$emit('scroll', pos)
        })
      }
      // // 
      // if (this.pullRefreshObj.pullup) {
      //   this.scroll.on('scrollEnd', () => {
      //     // 
      //     if (this.scroll.y <= this.scroll.maxScrollY) {}
      //   })
      // }
      // 
      if (this.pullRefreshObj.pulldown || this.pullRefreshObj.pullup) {
        this.scroll.on('touchEnd', pos => {
          // console.log(this.scroll)
          // console.log(pos.y)
          // 
          if (pos.y > 50) {
            this.$emit('pulldown')
            this.pullDownStyle = Math.min(pos.y - 50, 40)
            this.beforePullDown = false
            this.pullRefreshObj.finishPullDown = false
            this.initPullUp()
          } else if (pos.y < this.scroll.maxScrollY - 30) {
            // 
            if (!this.pullRefreshObj.noMoreData) {
              this.pullRefreshObj.finishPullUp = false
              this.initPullDown()
              this.$emit('pullup')
            }
          }
        })
      }
      // 
      if (this.beforeScroll) {
        this.scroll.on('beforeScrollStart', () => {
          this.$emit('beforeScroll')
        })
      }
    },
    initPullDown () {
      this.bubbleY = 0 // canvas
      this.beforePullDown = true  // 
      this.pullDownStyle = 0  // 
    },
    initPullUp () {
      this.pullRefreshObj.noMoreData = false // 
      this.pullRefreshObj.finishPullUp = true // 
    },
    disable () {
      // better-scrolldisable
      this.scroll && this.scroll.disable()
    },
    enable () {
      // better-scrollenable
      this.scroll && this.scroll.enable()
    },
    refresh () {
      // better-scrollrefresh
      this.scroll && this.scroll.refresh()
    },
    scrollTo () {
      // better-scrollscrollTo
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
    },
    scrollToElement () {
      // better-scrollscrollToElement
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
    }
  },
  watch: {
    // refreshDelayrefresh
    data () {
      this.contentHeight = 'auto'
      setTimeout(() => {
        this.refresh()
        if (this.$refs.content.clientHeight < this.$refs.wrapper.clientHeight) {
          this.contentHeight = '101%'
        } else {
          this.contentHeight = 'auto'
        }
        // console.log(this.$refs.content.clientHeight)
        // console.log(this.$refs.wrapper.clientHeight)
      }, this.refreshDelay)
    },

    'pullRefreshObj.finishPullDown': function (newVaule) {
      if (newVaule) {
        setTimeout(() => {
          this.initPullDown()
          this.refresh()
        }, 500)
      }
    }
  }
}
</script>

<style scoped lang="less">
@import '../../style/mixin.less';
.scroll{
  .wh(100%, 100%);
  .content{
    .wh(100%, auto);
    padding: 15px 0 40px 0;
    position: relative;
    .top_tip{
      .wh-l(100%, 60px);
      position: absolute;
      left: 0;
      top: -60px;
      z-index: -1;
      font-size: 14px;
      .after_trigger{
        .wh(100px, 20px);
        margin: 0 auto;
        position: relative;
        top: 40px;
        .msg_icon{
          .wh(20px, 20px);
          margin: 0 auto;
          img{
            width: 100%;
          }
        }
        .msg_txt{
          .wh(100%, 100%);
          line-height: normal;
        }        
      }
    }
    .bottom_tip{
      .wh-l(100%, 50px);
      position: absolute;
      left: 0;
      bottom: 0px;
      z-index: -1;
      font-size: 14px;
      .after_trigger{
        .wh(100px, 20px);
        margin: 0 auto;
        position: relative;
        top: 20px;
        .msg_icon{
          .wh(20px, 20px);
          margin: 0 auto;
          img{
            width: 100%;
          }
        }
      }
    }
  }
}
</style>



