<template>
  <div class="configfailed">
       <head-top title="">
         <div slot="setLeft" class="bluetooth" @click="next">
            <img src="../../image/global/back.png">
          </div>
       </head-top>
       <div class="contactimg">
         <img src="../../image/adddirver/Cancel@3x.png">
       </div>  
       <div class="prompt"> 
           <p>1 WiFi2.4G5GWIFI</p>
           <p>2 WiFi</p>
           <p>3 </p>
           <p>4 mac)</p>
           <p>5 </p>
       </div>  
       <div class="nextbtn" @click="next">
           <span></span>
       </div>   
  </div>
</template>
<script>
import { mapState } from 'vuex'
import headTop from '../../components/header/header'

export default {
  data () {
    return {
    }
  },
  created () {

  },
  mounted () {

  },

  components: {
    headTop
  },

  computed: {
    ...mapState([
      'transitiondre'
    ])
  },

  methods: {
    next () {
      this.$store.state.transitiondre = true
      this.$router.go(-2)
    }
  }
}
</script>
<style lang="less" scoped>
 @import '../../style/mixin.less';
  .configfailed{
   .fs;
   background: #ffffff;
   .bluetooth{
      .wh(1.066667rem, 100%);
      margin-left: .2rem;
      img{
        height: .533333rem;
        vertical-align: middle;
      }
    }
   .contactimg{
     .wh(100%,auto);
     margin-top:88px;
     display: flex;
     justify-content: center;
     img{
      .wh(100px,100px)
     }
    }
   .prompt{
      width:305px;
      margin: 0 auto;
      margin-top: 70px;
      text-align: left;
      p{
          color: #000;
          margin-top: 18px;
          font-size: 12px;
      }
    }
     .nextbtn{
      .wh-l(240px,40px);
      margin: 0 auto;
      margin-top: 26px;
      border-radius: 40px;
      background:  @themeColor;
      text-align: center;
      span{
        .wh(100%, 100%);
        line-height: 40px;
        display: block;
        font-size: 17px;
        color: #fff;
        vertical-align: middle;
      }
    }
  }
 </style>


