Object.keys(object).map(function (key) {
  object[key].map(function (value) {
    zhErrorCode[value] = zhTranslateCode[key]
    enErrorCode[value] = enTranslateCode[key]
  })
})
// console.log(zhErrorCode)
// console.log(enErrorCode)

const errorHandle = (code) => {
  if (errorCode[code]) {
    console.log('errorCode:')
    // 
    store.dispatch('setErrorCode', code)
    if (i18n.locale === 'zh') {
      store.dispatch('alertErrorMsg', zhErrorCode[code])
    } else {
      store.dispatch('alertErrorMsg', enErrorCode[code])
    }
  }
}

export { errorHandle }



