import Vue from 'vue'
import Vuex from 'vuex'
import getters from './getters'
import mutations from './mutations'
import user from './modules/user'
import errorLog from './modules/errorLog'
import app from './modules/app'

Vue.use(Vuex)

const state = {
  errorMsg: '', // 
  LoginState: null, // 
  currentRouter: null, // 
  actionSheet: false, // 
  actionSheetType: null, // 
  ToastType: null, // 
  messageBox: false, // 
  messageBoxType: null, // 
  equipmentData: [], // 
  currentDevice: null, // 
  progressWidth: 0, // 
  socketStatusData: null, // 
  socketSwitch: false, // 
  disabledeter: null, // 
  countDownOFF: false, // 
  temperatureOFF: false, // 
  timingOff: false, // 
  spendingCountdownOff: false, // 
  electLocal: '0 0 0', // 
  settingData: {
    voltage: 0,
    electricity: 0,
    power: 0
  }, // 
  TemperatureUnit: 1, //  1  2 
  montaryUint: 1, // 
  deviceState: 0, // 
  transitiondre: false, // 
  userInfo: null, // 
  virtualExperience: false, // 
  statePage: {
    stateCountdown: 0, // 
    Timer: null, // 
    countdownRotate: 0, // 
    countdownTimeText: '00:00', // 
    countdownText: '0/0/0' // WIFI state
  },
  firstRunning: true, // 
  countDownRange: 0, // 
  hasTask: false, // 
  currentTemperature: 0, // 
  tTargetValue: 26, // 
  timingRotate: 0, // 
  isHeating: false, // 
  disablemask: true, // 
  startActive: false, // 
  tStartActive: false, // 
  hStartActive: false, // 
  countdownValue: 0, // 
  powerActive: true, // 
  taskMinnte: 0, // 
  taskHour: 0, // 
  taskSwitch: false, // 
  currentTask: '', // 
  ordinaryData: [], // 
  advancedData: [], // 
  taskData: [], // 
  deviceListData: [], // 
  noNetwork: true, // 
  findPage: null, // 
  weekarr: [], // 
  jumpTimer: '' // 
}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  modules: {
    user,
    errorLog,
    app
  }
})



