/**
 *  Error
 * @method errorHandlerRequest
 * @param  {string}   navigator   
 * @param  {object}   self        this
 */

export const errorHandlerRequest = (navigator, self, error) => {
  if (self.$root.ISDEBUG) return false
  switch (navigator) {
    case 'Android':
      // console.log('Android')
      window.Error.errorHandlerRequest(error)
      break
    case 'IOS':
      // console.log('IOS')
      let obj = {
        'error': error
      }
      window.webkit.messageHandlers.errorHandlerRequest.postMessage(obj)
      break
    case 'Wechat':
      // console.log("Wechat")
      break
  }
}

